form {
    background: #fff;
    padding: 1rem;
    @include curveAllCorners;
    @include boxShadow($silver);
}

form.navbar__link {
    box-shadow: none;
}

.form-field {
    margin: 1rem 0;
    position: relative;

    & input {
        height: 2.2rem;
        border: 1px solid $silver;
    }

    & input,
    & textarea {
        width: 100%;
        // padding: 0.5rem;
        margin-bottom: 0.5rem;
        background: $transparent-red;
        border-radius: 4px;
        padding: 3.25em 0.5em 1.2em;
    }

    & input[type=checkbox],
    & input[type=checkbox]:checked {
        height: 20px;
        width: 20px;
        background-color: $transparent-red;
    }
}

.form-field__label {
    display: block;
    font-weight: 700;
    margin-bottom: 0.25rem;
    position: absolute;
    top: 8px;
    left: 8px;
}

.form-field--alt {
    background: rgba(211, 67, 62, 0.1);
    border-radius: 4px;
    padding: 1.2em 0.5em 1.2em;
    border: 1px solid #bfbfbf;
}

.form-field--alt .form-field__label {
    position: initial;
    float: left;
    margin-right: 1rem;
}

.form-field__label--required {
    font-size: 0.75rem;
}

.form-field--remember {
    display: flex;
    align-items: center;

    &>label {
        margin-right: 0.5rem;
    }
}

// .form-field__label[for=rsvp] {
//     display: inline;
// }

input[type=file] {
    // padding: 0.32rem 0 0 0.2rem;
    padding: 3em 0.5em 2em;
}

input[type=checkbox],
input[type=checkbox]:checked {
    width: 1rem;
    height: 1rem;
}

.form-field__help-text {
    font-size: 0.9rem;
    padding-left: 0.2rem;
}

.alert {
    padding: 1em 0.5em;
    // border: 1px solid;
    margin: 1em 0;
    @include curveAllCorners;
    @include boxShadow($purple);
}

.alert--danger {
    background: $error;
    color: #fff;
}

.form-field--description {
    width: 100%;
    margin-bottom: 0.5rem;
    background: rgba(211, 67, 62, 0.1);
    border-radius: 4px;
    padding: 3.25em 0.5em 1.2em;
    border: 1px solid $silver;
}



form.email_list .blockLabels {
    padding: 1rem 0 0 0;
    border: none;
}

form.email_list .buttonHolder {
    padding: 1.5rem 0 0 0;
}

.check_field {
    display: inline-flex;
    flex-direction: row-reverse;
    align-items: center;
    margin: 0 1rem 0 0;
    flex-wrap: wrap;

    &>.form-field__label {
        position: initial;
    }

    &>input[type=checkbox] {
        margin-right: 0.5rem;
    }
}

.form-field.check_fields,
.form-field--multiselect {
    background: rgba(211, 67, 62, 0.1);
    border-radius: 4px;
    padding: 3.25em 0.5em 1.2em;
    width: 100%;

    border: 1px solid #bfbfbf;
}

.form-field--multiselect {
    // &>label {
    //     position: initial;
    // }
    height: 10rem;

    &>.check_field {
        max-height: 6.5rem;
        overflow-y: auto;
        width: 100%;
        flex-direction: initial;

        &>ul#id_categories {
            list-style-type: none;
            padding-left: 0;

            &>li {
                margin-bottom: 0.5rem;

                &>label {
                    display: flex;
                    align-items: center;

                    &>input {
                        margin-right: 0.5rem;
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}

.form-field__label>.error {
    color: red;
    font-weight: bold;
}
