.panel {
    background-color: $white;
    padding: 1rem;
    margin: 2rem 0;
    @include curveAllCorners;
    @include boxShadow($silver)
}

.panel__banner {
    margin: 0 0 3rem 0;
    height: 22rem;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

// .panel__title {
//     margin: 0;
// }

.panel__content {
    margin: 1rem auto 0;
    max-width: 800px;
}

.panel__text {
    margin: 1.5rem 0;
}
